.link {
    background-color: #FFFFFF;
    color: black;
    border-radius: 5px;
    cursor: pointer;    
    height: 20px; /* Set a fixed height */
    line-height: 20px; /* Center the text vertically */
    display: inline-block;
  }

.link:hover {
    color: #FF645A;
    font-size: 1.02em;    
}

.logoImg{
    width: 140px;
    display: inline-block;
  }

.button {
    border-radius: 10px;
    padding: 10px 10px 10px 10px;
    font-size: 1.1em; 
    font-family: inherit;
    color: #ffffff; 
    background-color: #7270e0;
    border: none;
}

.button:hover {
    background-color: #4b2ef3;
}

.auth-form-container, .login-form, .register-form {
    width: 500px;
}


.left-side {
    text-align: center;
    margin-right: 20px; /* Add margin for spacing */
}

.right-side {
    width: 500px; /* Set the width of the login/register form */
    display: flex;
    flex-direction: column;
    align-items: center;
}

label {
  text-align: left;
  padding: 0.25rem 0;
}

input {
  margin: 0.5rem 0;
  padding: 10px;
  border-radius: 10px;
}

select {
  margin: 0.5rem 0;
  padding: 10px;
  border-radius: 10px;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 90px);
    background: url('C:\Users\nagul\Desktop\term_pro\app\frontend\public\car_imgs\audi1.png') no-repeat center center fixed;
    background-size: cover;
    backdrop-filter: blur(8px);
  }

  .form-box {
    border: 1px solid #ddd; /* Light gray border */
    background-color: rgba(255, 255, 255, 0.5); /* White background */
    padding: 20px;
    border-radius: 8px;
    margin-bottom: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Light shadow for depth */
  }

